* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
}

.country-list-item {
    display: flex;
    align-items: center;
    padding: 10px;
}

.country-img {
  margin-right: 10px;
}

.country-list-name {
  font-size: 26px;
  font-weight: bold; 
}

.info-country-list {
  font-size: 18px;
}

.main-info {
    font-size: 18px;
    font-weight: bold;
    text-decoration: #253342;
}